import React from 'react';
import Me from '../../assets/me.jpg';
import { FaAward } from 'react-icons/fa';
import { BiCodeAlt } from 'react-icons/bi';
import './about.css';
export default function About(){
  return(
    <section id="about" className="container-wrapper">
      <h5>About Me</h5>
      <h2>Sobre mim</h2>
      <div className="container about-container">
        <div className="about-me">
          <div className="about-me-img">
            <img src={Me} alt="Minha Foto" />
          </div>
        </div>
        <div className="about-content">
          <div className="about-cards">
            <article className="about-card">
              <FaAward className="about-icon"/>
              <h5>Experiência de Mercado</h5>
              <small>Mais de 10 anos</small>
            </article>
            <article className="about-card">
              <BiCodeAlt className="about-icon"/>
              <h5>Projetos Profissionais</h5>
              <small>21</small>
            </article>
            <article className="about-card">
              <BiCodeAlt className="about-icon"/>
              <h5>Projetos Pessoais</h5>
              <small>5</small>
            </article>
          </div>
          <p>
            Sou Analista de Sistemas DevOps, apaixonado por tecnologia, cativado pela área de desenvolvimento, engenharia e operações, venho me aprimorando mais em arquitetura e desenvolvimento.
            Estudo IaC, PaaS e SaaS com interfaces modernas visando a melhor experiência.
          </p>
          <a href="#contact" className="btn-about-me">Contato</a>
        </div>
      </div>
    </section>
  );
}